// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'

const CalculatorComponent = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	padding: 30px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`
const Result = styled.input`
	width: 100%;
	max-width: 500px;
	font-size: 50px;
	font-weight: bold;
	margin: 10px;
	text-align: center;
	border: none;
`
const Buttons = styled.div`
	width: 100%;
	max-width: 500px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 10px;
`
const Button = styled.button`
	font-size: 25px;
`

class Calculator extends Component {
	state = {
		result: ''
	}
	insert = (e) => {
		e.persist()
		this.setState(prevState => ({
			result: prevState.result + (prevState.result || !isNaN(Number(e.target.value)) ? e.target.value : '')
		}));
	}
	calc = () => {
		/* eslint no-eval: 0 */
		this.setState(prevState => ({
			result: eval(prevState.result).toString().replace(/[0-9]*$/, x => {
				// console.log(x, x.length,x.charAt(x.length - 1), x.charAt(x.length - 2));
				if (x.length === 16 && (x.charAt(x.length - 2) === x.charAt(x.length - 1) || Number(x.charAt(x.length - 2)) === Number(x.charAt(x.length - 1)) + 1)) {
					return `(${x.charAt(0)})`
				}
				else return x
			})
		}));
	}
	render() {
		return (
			<CalculatorComponent>
				<Result readOnly value={!this.state.result ? '0' : this.state.result} />
				<Buttons>
					<Button value="7" onClick={this.insert}>7</Button>
					<Button value="8" onClick={this.insert}>8</Button>
					<Button value="9" onClick={this.insert}>9</Button>
					<Button value="/" onClick={this.insert}>/</Button>

					<Button value="4" onClick={this.insert}>4</Button>
					<Button value="5" onClick={this.insert}>5</Button>
					<Button value="6" onClick={this.insert}>6</Button>
					<Button value="*" onClick={this.insert}>x</Button>

					<Button value="3" onClick={this.insert}>3</Button>
					<Button value="2" onClick={this.insert}>2</Button>
					<Button value="1" onClick={this.insert}>1</Button>
					<Button value="-" onClick={this.insert}>-</Button>
					
					<Button value="0" onClick={this.insert}>0</Button>
					<Button value="." onClick={this.insert}>.</Button>
					<Button value="+" onClick={this.insert}>+</Button>
					<Button value="=" onClick={this.calc}>=</Button>
				</Buttons>
			</CalculatorComponent>
		);
	}
}

export default Calculator